import React from "react"
import { Trans } from "gatsby-plugin-react-i18next"
import Facebook from "../assets/svg/facebook.svg"
import Twitter from "../assets/svg/twitter.svg"
import Codepen from "../assets/svg/codepen.svg"
import Linkedin from "../assets/svg/linkedin.svg"
import Github from "../assets/svg/github.svg"

const Footer = ({ footerSocialLinks }) => (
  <footer className="footer">
    <section className="footer-section">
      <h2> Yunet Luis Ruíz </h2>
      <div className="container">
        <div className="info-general">
          <p>
            <Trans i18nKey="footer:info">
              I'm a software developer living in Vienna, Austria. I have a great
              passion for my work and the new technologies in general.
            </Trans>
          </p>
        </div>
        <div className="info-moreover">
          <strong><Trans i18nKey="footer:more">MOREOVER</Trans></strong>
          <p>
            <Trans i18nKey="footer:more-desc">
              I like to make cool and creative designs, regardless of what medium
              I'm working in.
            </Trans>
          </p>
        </div>
        <div className="info-social-links">
          <SocialLinks footerSocialLinks={footerSocialLinks} />
        </div>
      </div>
    </section>
  </footer>
)

const SocialLinks = ({ footerSocialLinks }) => (
  <ul>
    {footerSocialLinks.map(link => (
      <li key={link.name}>
        <a href={link.href} target="_blank" rel="noopener noreferrer">
          {verifySocialLink(link.name)}
        </a>
      </li>
    ))}
  </ul>
)

const verifySocialLink = linkName => {
  switch (linkName) {
    case "Facebook":
      return <Facebook />
    case "Twitter":
      return <Twitter />
    case "Codepen":
      return <Codepen />
    case "Linkedin":
      return <Linkedin />
    case "Github":
      return <Github />
    default:
      console.log(linkName)
  }
}

export default Footer
