import React from "react"
import { graphql, StaticQuery } from "gatsby"
import Header from "./Header"
import Footer from "./Footer"
import SEO from "./SEO"
import "../styles/main.scss"

const Layout = ({ children, title }) => (
  <StaticQuery
    query={layoutQuery}
    render={data => (
      <React.Fragment>
        <SEO title={title}/>
        <Header
          title= {title}
          menuLinks={data.site.siteMetadata.menuLinks}
          headerIcons={data.site.siteMetadata.headerIcons}
        />
        {children}
        <Footer footerSocialLinks={data.site.siteMetadata.footerSocialLinks} />
      </React.Fragment>
    )}
  />
)

const layoutQuery = graphql`
  query SiteMetadata {
    site {
      siteMetadata {
        menuLinks {
          name
          link
        }
        headerIcons {
          name
        }
        footerSocialLinks {
          name
          href
        }
      }
    }
  }
`

export default Layout
